







class Slice {


    constructor(options) {
        // 
        this.file = options.file;
        // 
        this.totalSize = this.file.size;
        // 
        this.chunkSile = options.chunkSile * 1024 * 1024;
        // 
        this.chunkLength = 0;
        // 
        this.slices = [];

        // 
        this.start = 0;
        // 
        this.end = 0;

        this._compute();
    }


    /**
     * @description 
     * @return {Object} this
     * */
    _compute() {
        // console.log('' + this.totalSize);
        if (this.totalSize <= this.chunkSile) {
            this.chunkLength = 1;
        } else {
            this.chunkLength = Math.ceil(this.totalSize / this.chunkSile);
        }
        // console.log(this.chunkLength);
    }

    /**
     * @description 
     * @return {Array} 
     * */
    getSlice() {
        if (this.slices.length !== 0) {
            return this.slices;
        }
        for (let i = 1; i <= this.chunkLength; i++) {
            // 
            if (i === this.chunkLength || this.chunkLength === 1) {
                this.end = this.totalSize;
            } else {
                this.end = i * this.chunkSile;
            }
            // 
            let blob = this.file.slice(this.start, this.end);

            this.slices.push({
                blob,
                order: i,
                start: this.start,
                end: this.end
            });
            this.start = this.end;
        }
        return this.slices;
    }


    /**
     * @description 
     * @return {Number}
     * */
    getSize() {
        return this.totalSize;
    }

    /**
     * @description 
     * @return {Number}
     * */
    getChunkLength() {
        return this.chunkLength;
    }


}

export default Slice;








